/****************************************************
base
****************************************************/
body { margin: 0px; padding: 0px; font-family: 'montserrat', Arial, sans-serif; background-color: #fff; height: 100%; line-height: 1; position: relative; font-size: 16px !important; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; }

/*MAIN*/
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; font-size: 100%; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section, main { display: block; }

blockquote, q { quotes: none; }

blockquote:before, blockquote:after, q:before, q:after { content: ''; content: none; }

table { border-collapse: collapse; border-width: 0px; padding: 0px; margin: 0px; }

html { height: 100%; -webkit-text-size-adjust: none; -ms-text-size-adjust: none; }

input, textarea { color: #333; font: 16px 'montserrat', Arial, sans-serif; outline: none; border-radius: 0; -moz-border-radius: 0; -webkit-border-radius: 0; -webkit-appearance: none; }

input[type="button"], input[type="submit"], button { cursor: pointer; }

td { margin: 0px; padding: 0px; }

form { padding: 0px; margin: 0px; }

a { transition: all 0.3s; text-decoration: none; outline: none; -webkit-tap-highlight-color: rgba(0, 0, 0, 0); color: #000; }

a, span, div, button { outline: none !important; }

input[type=submit], input[type=button], button { -webkit-appearance: none; outline: none; }

* { -webkit-box-sizing: border-box; -moz-box-sizing: border-box; box-sizing: border-box; }

*:before, *:after { -webkit-box-sizing: border-box; -moz-box-sizing: border-box; box-sizing: border-box; }

* { margin: 0; padding: 0; }

:invalid { box-shadow: none; }

ol, ul { list-style-position: inside; }

::-webkit-input-placeholder { color: #000; opacity: 1; transition: opacity 0.3s ease; }

::-moz-placeholder { color: #000; opacity: 1; transition: opacity 0.3s ease; }

:-moz-placeholder { color: #000; opacity: 1; transition: opacity 0.3s ease; }

:-ms-input-placeholder { color: #000; opacity: 1; transition: opacity 0.3s ease; }

:focus::-webkit-input-placeholder { opacity: 0; transition: opacity 0.3s ease; }

:focus::-moz-placeholder { opacity: 0; transition: opacity 0.3s ease; }

:focus:-moz-placeholder { opacity: 0; transition: opacity 0.3s ease; }

:focus:-ms-input-placeholder { opacity: 0; transition: opacity 0.3s ease; }

.pull-left { float: left; }

.pull-right { float: right; }

.align-left { text-align: left; }

.align-center { text-align: center; }

.align-right { text-align: right; }

.overflow-hidden { overflow: hidden; }

.hide { display: none; }

.clearfix:before { content: ""; display: table; }

.clearfix:after { content: ""; display: table; clear: both; }

svg { max-width: 100%; max-height: 100%; }

.main-wrapper { padding: 0 0 0 0; min-width: 320px; width: 100%; position: relative; overflow: hidden; min-height: 100%; display: -webkit-box; display: -moz-box; display: -ms-flexbox; display: -webkit-flex; display: flex; flex-direction: column; -webkit-flex-direction: column; -ms-flex-direction: column; justify-content: flex-start; -webkit-justify-content: flex-start; -ms-flex-pack: start; }

.wrapper { min-width: 320px; max-width: 1760px; padding: 0 40px 0 40px; margin: 0 auto; position: relative; }

.text-left { text-align: left !important; }

.text-center { text-align: center !important; }

.text-right { text-align: right !important; }

.nowrap { white-space: nowrap !important; }

.content { min-width: 320px; text-align: left; width: 100%; order: 2; -webkit-order: 2; flex-grow: 1; -webkit-flex-grow: 1; }

/****************************************************
uikit
****************************************************/
h1 { font-weight: bold; font-style: normal; font-size: 52px; line-height: 60px; padding: 15px 0; }

h2 { font-weight: bold; font-style: normal; font-size: 36px; line-height: 44px; margin-bottom: 15px 0; }

h3 { font-weight: bold; font-style: normal; font-size: 28px; line-height: 44px; padding: 15px 0; }

h4 { font-weight: bold; font-style: normal; font-size: 20px; line-height: 30px; padding: 15px 0; }

.tags { line-height: 16px; }

.tags a { color: #8F8F8F; font-size: 12px; line-height: 16px; }

.ui-list { margin: 0 -30px; }

.ui-list__item { display: flex; align-items: center; margin-bottom: 10px; padding: 0 30px; }

.ui-list__item:last-child { margin-bottom: 0; }

.ui-list__tags { width: 50%; }

.ui-list__value { font-weight: 300; font-size: 14px; line-height: 20px; color: #000000; width: 50%; }

/****************************************************
fonts
****************************************************/
@font-face { font-family: 'montserrat'; src: url("../fonts/montserrat-bold-webfont.eot"); src: url("../fonts/montserrat-bold-webfont.eot?#iefix") format("embedded-opentype"), url("../fonts/montserrat-bold-webfont.woff2") format("woff2"), url("../fonts/montserrat-bold-webfont.woff") format("woff"), url("../fonts/montserrat-bold-webfont.ttf") format("truetype"), url("../fonts/montserrat-bold-webfont.svg#montserratbold") format("svg"); font-weight: 700; font-style: normal; }

