@font-face { font-family: 'montserrat'; src: url("../fonts/montserrat-semibold-webfont.eot"); src: url("../fonts/montserrat-semibold-webfont.eot?#iefix") format("embedded-opentype"), url("../fonts/montserrat-semibold-webfont.woff2") format("woff2"), url("../fonts/montserrat-semibold-webfont.woff") format("woff"), url("../fonts/montserrat-semibold-webfont.ttf") format("truetype"), url("../fonts/montserrat-semibold-webfont.svg#montserratsemibold") format("svg"); font-weight: 600; font-style: normal; }

/****************************************************
buttons
****************************************************/
.btn { display: inline-flex; align-items: center; justify-content: center; box-sizing: border-box; transition: .3s; text-decoration: none; cursor: pointer; }

.btn-main { height: 56px; font-size: 16px; font-weight: 700; line-height: 24px; width: 240px; padding-left: 15px; padding-right: 15px; background: #000; color: #fff; }

.btn-main_mod-white { background-color: #fff; color: #000; }

.btn-standart { position: relative; font-weight: 700; height: 45px; padding-left: 20px; padding-right: 20px; min-width: 160px; }

.btn-standart__figure1:before { content: ""; position: absolute; left: 0; top: 0; width: 2px; height: 16px; background: #000; transition: all 0.3s; }

.btn-standart__figure1:after { content: ""; position: absolute; left: 0; top: 0; width: 50px; height: 2px; background: #000; transition: all 0.3s; }

.btn-standart__figure2:before { content: ""; position: absolute; right: 0; bottom: 0; width: 2px; height: 16px; background: #000; transition: all 0.3s; }

.btn-standart__figure2:after { content: ""; position: absolute; right: 0; bottom: 0; width: 50px; height: 2px; background: #000; transition: all 0.3s; }

.btn-standart:hover { color: #333; }

.btn-standart:hover span:before, .btn-standart:hover span:after { background-color: #333; }

.btn-standart:active { color: #000; }

.btn-standart:active span:before, .btn-standart:active span:after { background-color: #000; }

.btn-standart.disabled, .btn-standart:disabled { color: #8F8F8F; }

.btn-standart.disabled span:before, .btn-standart.disabled span:after, .btn-standart:disabled span:before, .btn-standart:disabled span:after { background-color: #8F8F8F; }

.btn-search { width: 16px; height: 16px; }

.link-more { display: inline-flex; align-items: center; font-size: 16px; line-height: 24px; font-weight: 700; }

.link-more .icon { flex-shrink: 0; margin-left: 10px; width: 8px; height: 12px; }

.link-file { display: inline-flex; align-items: center; font-size: 14px; }

.link-file .icon { flex-shrink: 0; margin-right: 15px; width: 16px; height: 16px; }

.link-file__text { line-height: 18px; border-bottom: 1px solid rgba(0, 0, 0, 0.2); transition: .3s; }

.link-file:hover .link-file__text { border-color: transparent; }

/* Slider */
.slick-slider { position: relative; display: block; box-sizing: border-box; -webkit-touch-callout: none; -webkit-user-select: none; -khtml-user-select: none; -moz-user-select: none; -ms-user-select: none; user-select: none; -ms-touch-action: pan-y; touch-action: pan-y; -webkit-tap-highlight-color: transparent; }

.slick-list { position: relative; overflow: hidden; display: block; margin: 0; padding: 0; }

.slick-list:focus { outline: none; }

.slick-list.dragging { cursor: pointer; cursor: hand; }

.slick-slider .slick-track, .slick-slider .slick-list { -webkit-transform: translate3d(0, 0, 0); -moz-transform: translate3d(0, 0, 0); -ms-transform: translate3d(0, 0, 0); -o-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0); }

.slick-track { position: relative; left: 0; top: 0; display: block; margin-left: auto; margin-right: auto; min-width: 100%; }

.slick-track:before, .slick-track:after { content: ""; display: table; }

.slick-track:after { clear: both; }

.slick-loading .slick-track { visibility: hidden; }

.slick-slide { float: left; height: 100%; min-height: 1px; outline: none; display: none; }

[dir="rtl"] .slick-slide { float: right; }

.slick-slide.slick-loading img { display: none; }

.slick-slide.dragging img { pointer-events: none; }

.slick-initialized .slick-slide { display: block; }

.slick-loading .slick-slide { visibility: hidden; }

.slick-vertical .slick-slide { display: block; height: auto; border: 1px solid transparent; }

.slick-arrow.slick-hidden { display: none; }

/****************************************************
header
****************************************************/
header { width: 100%; min-width: 320px; text-align: left; order: 1; -webkit-order: 1; opacity: 0; }

header .wrapper { display: flex; justify-content: space-between; align-items: center; padding-top: 40px; padding-bottom: 40px; border-bottom: 1px solid #F6F6F6; }

.header-bar { display: flex; align-items: center; flex-wrap: wrap; margin: 0 -27px; }

.header-bar__item { padding: 0 27px; }

.header-nav { display: flex; flex-wrap: wrap; list-style: none; }

.header-nav__item { margin: 0 30px; }

.header-nav__link { font-size: 14px; line-height: 20px; font-weight: 700; }

.link-region { border-bottom: 1px dotted; font-size: 14px; line-height: 20px; }

.link-region:hover { border-color: transparent; }

.header-phone { font-size: 14px; }

.link-menu { display: flex; align-items: center; }

.link-menu__icon { flex-shrink: 0; width: 24px; height: 11px; margin-right: 14px; }

/****************************************************
footer
****************************************************/
footer { background: #000 url(../img/footer-bg.svg) no-repeat right bottom; color: #fff; text-align: left; width: 100%; min-width: 320px; order: 3; -webkit-order: 3; }

footer .wrapper { display: flex; }

.footer-main { width: 640px; flex-shrink: 0; box-sizing: border-box; position: relative; margin: 40px 0 40px -80px; position: relative; align-self: flex-start; }

.footer-main__inner { position: relative; z-index: 10; background: #333; padding: 40px 80px; }

.footer-main__elem { position: absolute; left: 10px; top: 10px; width: 100%; height: 100%; border: 1px solid #fff; box-sizing: border-box; z-index: 2; }

.footer-request { border-left: 1px solid #333; padding: 40px 80px; display: flex; flex-direction: column; justify-content: center; }

.footer-request__text { margin-bottom: 40px; }

.footer-desc { margin: 20px 0; font-size: 14px; line-height: 20px; }

.footer-copyright { color: #8F8F8F; font-size: 14px; line-height: 20px; }

