/****************************************************
elements
****************************************************/
.breadcrumbs { list-style: none; display: flex; flex-wrap: wrap; margin-bottom: 40px; }

.breadcrumbs > li { margin-right: 37px; position: relative; }

.breadcrumbs > li > a { font-size: 14px; color: #8F8F8F; }

.breadcrumbs > li:last-child > a { color: #000; }

.breadcrumbs > li:last-child:after { display: none; }

.breadcrumbs > li:after { content: ""; position: absolute; right: -20px; top: 11px; width: 4px; height: 4px; border: 1px solid #D6D6D6; }

.css-slider-theme .slick-arrow { position: absolute; width: 60px; height: 60px; z-index: 50; display: flex; align-items: center; justify-content: center; border: 1px solid transparent; transition: .5s; box-sizing: border-box; background: #fff; }

.css-slider-theme .slick-arrow .icon { width: 30px; height: 9px; fill: #8F8F8F; transition: .5s; }

.css-slider-theme .slick-arrow:hover { border-color: #000; }

.css-slider-theme .slick-arrow:hover .icon { fill: #000; }

.css-slider-gallery .slick-arrow { top: calc(100% + 50px); width: auto; height: auto; background-color: transparent; border: 0; }

.css-slider-gallery .slick-prev { left: 0; }

.css-slider-gallery .slick-next { right: 0; }

.btn-full .btn { width: 100%; }

.field { position: relative; margin-bottom: 37px; }

.field:last-child { margin-bottom: 0; }

.field input { border-bottom: 2px solid #D6D6D6; height: 40px; }

.field__label { position: absolute; left: 0; top: 0; font-size: 12px; line-height: 16px; }

.checkbox { display: flex; align-items: flex-start; position: relative; font-size: 16px; line-height: 24px; }

.checkbox__main { cursor: pointer; }

.checkbox input:checked + .checkbox__elem { border-color: transparent; }

.checkbox input:checked + .checkbox__elem .icon { opacity: 1; }

.checkbox__elem { display: inline-flex; align-items: center; justify-content: center; flex-shrink: 0; cursor: pointer; width: 16px; height: 16px; box-sizing: border-box; border: 2px solid #D6D6D6; margin-right: 15px; position: relative; transition: .3s; bottom: -3px; }

.checkbox__elem .icon { position: absolute; left: -2px; top: -2px; opacity: 0; width: 16px; height: 16px; max-width: 16px; max-height: 16px; transition: .3s; fill: #000; }

