page: cases
****************************************************/
.cases-wrapper { display: flex; flex-wrap: wrap; margin-left: -20px; margin-right: -20px; }

.cases-wrapper__col { padding-left: 20px; padding-right: 20px; width: 25%; box-sizing: border-box; }

