/****************************************************
page: services
****************************************************/
.solution-item {
	margin-bottom: 44px;
	&__img {
		height: 250px;
		overflow: hidden;
		line-height: 0;
		img {
			width: 100%;
			max-height: none;
		}
	} 
	&__main {
		padding: 35px 35px;
		p {
			margin-bottom: 24px;
			&:last-child {
				margin-bottom: 0;	
			}
		}
	}
}

.section-solutions {
	.title {
		margin-bottom: 30px;
	}
}

.card-section {
	padding-top: 60px;
	padding-bottom: 60px;
}

.card-top-section {
	padding-top: 20px;
	padding-bottom: 20px;
}

.videos-wrapper {
	.video-item {
		margin-top: 0;	
		margin-bottom: 0;
	}
}

.card-parameters {
	font-size: 14px;
	line-height: 20px;
	margin-bottom: 25px;
	&__row {
		display: flex;
		margin-bottom: 13px;
		&:last-child {
			margin-bottom: 0;	
		}	
		&>div {
			&:first-child {
				content: "";
			}
		}
	}
	&__dots {
		border-bottom: 1px dotted $gray2;
		display: block;
		flex-grow: 1;
	}
}

.link-border {
	border-bottom: 1px solid rgba(0,0,0,.2);
	font-weight: 600;	
	font-size: 14px;
	line-height: 20px;
	&:hover {
		border-color: transparent;	
	}
}

.text-note-mini {
	font-size: 12px;
	line-height: 16px;
	color: $gray;
	margin-bottom: 25px;
	&:last-child {
		margin-bottom: 0;
	}	
}

.list-dash {
	list-style: none;
	margin-top: 22px;
	margin-bottom: 30px;
	&>li {
		margin-bottom: 18px;
		position: relative;
		padding-left: 38px;
		&:last-child {
			margin-bottom: 0;
		}
		&:before {
			content: "";
			position: absolute;
			left: 0;
			top: 11px;
			width: 16px;
			height: 1px;	
			background: $black;
		}
	}
}


.top-bar {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-bottom: 40px;
	.title {
		margin-bottom: 0;
	}
}

.section-gray {
	background: $gray-light;
	&__section {
		padding-top: 85px;
		padding-bottom: 50px;
	}
	&__inner {
		padding-top: 80px;
		padding-bottom: 80px;
	}
}

.section-block {
	padding-bottom: 120px;
	&:first-child {
		padding-top: 80px;	
	}
	.tabs-menu {
		margin-bottom: 40px;	
	}
	&_set2 {
		padding-bottom: 70px;
	}
}






