/****************************************************
page: contacts
****************************************************/
.contacts-map {
	background: #ccc;
	&__block {
		background: #fff;
		padding: 50px 100px 120px;
		width: 710px;
		font-size: 18px;
		line-height: 28px;
	}
}

.contact-items-wrapper {
	border: 1px solid #000;
	padding: 30px;
}

.contacts-item {
	border-bottom: 1px solid #eee;
	padding-bottom: 20px;
	margin-bottom: 20px;
	&:last-child {
		border-bottom: 0;
		margin-bottom: 0;
		padding-bottom: 0;	
	}
	&__title {
		font-size: 20px;
		line-height: 24px;
		font-weight: 700;
		margin-bottom: 20px;
	}
	&__more {
		a {
			font-size: 14px;
			font-weight: 500;
			border-bottom: 1px dotted;
		}
	}
	&__row {
		margin-bottom: 15px;
		font-size: 16px;
		line-height: 156%;
		.icon {
			margin-bottom: -3px;
			width: 20px;
			height: 20px;
			margin-right: 10px;
		}
	}
}

.page-head {
	background: $gray-light;
	padding: 40px 0 60px;
	&__text {
		max-width: 960px;
		font-size: 16px;
		line-height: 28px;
	}
	&_mod2 {
		background: #fdfdfd;
		padding: 30px 0 75px;
		h1 {
			&:last-child {
				margin-bottom: 0;
			}
		}
	}
	.title {
		margin-bottom: 35px;
	}
}






















