/****************************************************
page: calculator
****************************************************/

.calculator-step {
	font-size: 24px;
	line-height: 29px;
	font-weight: 600;
	display: flex;
	&__value {
		font-weight: 600;
		font-size: 28px;
		line-height: 34px;
		color: $gray;
		margin: -7px 20px 0 0;
		&__current {
			font-size: 36px;
			line-height: 36px;
			color: $black;
		}
	}
	&__arrow {
		width: 30px;
		height: 12px;
		margin-right: 22px;
		margin-top: 3px;
	}
}

.calculator-title {
	font-size: 18px;
	line-height: 22px;
	color: $gray;
	margin-bottom: 23px;
}

.icon-help {
	width: 40px;
	height: 40px;
	border-radius: 100%;
	background: $red;
	color: #fff;
	display: flex;
	align-items: center;
	justify-content: center;
	font-size: 18px;
	line-height: 18px;
	margin-right: 10px;
}

.filter-footer {
	margin-top: 40px;
}

.calc-data {
	display: flex;
	margin-bottom: 44px;
	&__title {
		width: 275px;
		margin-right: 10px;
		flex-shrink: 0;
		font-weight: 700;
	}
	&__main {
		flex-grow: 1;
	}
}

.calc-table {
	width: 100%;
	border-collapse: collapse;
	font-size: 14px;
	line-height: 17px;
	th {
		background: #EEEEEE;
		color: $gray;
		font-weight: 400;
		text-align: left;
		padding: 11px 10px 10px;
		&:first-child {
			padding-left: 30px;	
		}
	}
	td {
		padding: 11px 10px 10px;
		border-bottom: 1px solid #EDECEC;
		&:first-child {
			padding-left: 30px;	
		}
		&:last-child {
			padding-right: 30px;
		}
	}
	&__title {
		font-weight: 600;
	}
	&__total {
		text-align: right;
		font-weight: 600;
		background: #EEEEEE;
	}

	&__th-mod {
		width: 14%;
		box-sizing: border-box;
	}
}

.calc-table-wrapper {
	border: 1px solid $black;
	&__title {
		padding: 8px 30px;
		font-weight: 700;
		border-bottom: 1px solid $black;
	}
}

.empty-value {
	font-weight: 200;
}













