/****************************************************
elements
****************************************************/
.breadcrumbs {
	list-style: none;
	display: flex;
	flex-wrap: wrap;
	margin-bottom: 40px;
	&>li {
		margin-right: 37px;
		position: relative;
		&>a {
			font-size: 14px;
			color: $gray;
		}
		&:last-child {
			&>a {
				color: $black;
			}
			&:after {
				display: none;
			}
		}
		&:after {
			content: "";
			position: absolute;
			right: -20px;
			top: 11px;
			width: 4px;
			height: 4px;
			border: 1px solid $gray2;
		}
	}
}

.css-slider-theme {
	.slick-arrow {
		position: absolute;
		width: 60px;
		height: 60px;
		z-index: 50;
		display: flex;
		align-items: center;
		justify-content: center;
		border: 1px solid transparent;
		transition: .5s;
		box-sizing: border-box;
		background: $white;
		.icon {
			width: 30px;
			height: 9px;
			fill: $gray;
			transition: .5s;
		}
		&:hover {
			border-color: $black;
			.icon {
				fill: $black;
			}
		}
	}
	
}

.css-slider-gallery {
	.slick-arrow {
		top: calc(100% + 50px);
		width: auto;
		height: auto;
		background-color: transparent;
		border: 0;
	}
	.slick-prev {
		left: 0;
	}
	.slick-next {
		right: 0;
	}
}

.btn-full {
	.btn {
		width: 100%;
	}
}



.field {
	position: relative;
	margin-bottom: 37px;
	&:last-child {
		margin-bottom: 0;	
	}
	input {
		border-bottom: 2px solid $gray2;
		height: 40px;
	}
	&__label {
		position: absolute;
		left: 0;
		top: 0;
		font-size: 12px;
		line-height: 16px;
	}
}

.checkbox {
	display: flex;
	align-items: flex-start;
	position: relative;
	font-size: 16px;
	line-height: 24px;
	&__main {
		cursor: pointer;
	}
	input {
		&:checked + .checkbox__elem {
			border-color: transparent;
			.icon {
				opacity: 1;
			}
		}
	}
	&__elem {
  		display: inline-flex;
  		align-items: center;
  		justify-content: center;
		flex-shrink: 0;
		cursor: pointer;
		width: 16px;
		height: 16px;
		box-sizing: border-box;
		border: 2px solid $gray2;
		margin-right: 15px;
		position: relative;
		transition: .3s;
		bottom: -3px;
		.icon {
			position: absolute;
			left: -2px;
			top: -2px;
			opacity: 0;
			width: 16px;
			height: 16px;
			max-width: 16px;
			max-height: 16px;
			transition: .3s;
			fill: $black;
		}
  	}
}




















