/****************************************************
page: catalog
****************************************************/

.catalog-content {
	display: flex;
	padding-bottom: 90px;
}

.catalog-side {
	width: 370px;
	flex-shrink: 0;
	margin-right: 80px;
	padding-top: 20px;
}


.card-item {
	border: 1px solid $black;
	transition: .3s;
	margin-bottom: 40px;
	box-sizing: border-box;
	max-width: 100%;
	.tags {
		min-height: 34px;
	}
	&__main {
		padding: 35px 40px 30px;
		&>div {
			&:last-child {
				margin-bottom: 0;	
			}
		}
	}
	&__title {
		margin-bottom: 24px;
		a {
			font-weight: 700;
			font-size: 20px;
			line-height: 30px;
			border-bottom: 1px solid rgba(0,0,0,.2);
			&:hover {
				border-color: transparent;
			}
		}
	}
	&__img {
		height: 239px;
		overflow: hidden;
		position: relative;
		&__overlay {
			display: flex;
			align-items: center;
			justify-content: center;
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.6) 89.84%);
			transition: .5s;
			opacity: 0;
			visibility: hidden;
			.link-more {
				color: #fff;
				.icon {
					fill: #fff;
				}
			}
		}
		img {
			min-height: 100%;
			max-width: 200%;
		}
	}

	&__desc {
		font-size: 14px;
		line-height: 24px;
		margin-bottom: 30px;
		&:last-child {
			margin-bottom: 0;	
		}
	}

	&__bar-info {
		display: flex;
		justify-content: space-between;
		border-top: 1px solid rgba(143,143,143,.2);
		padding: 17px 20px;
	}


	&:hover {
		box-shadow: 0 0 0 1px $black;
		.card-item {
			&__img {
				&__overlay {
					opacity: 1;
					visibility: visible;
				} 
			}
		}
	}

	&_material {
		.card-item {
			&__title {
				min-height: 60px;
			}
		}
		.card-price {
			margin-bottom: 41px;
		}
	}
	&_object {
		margin-bottom: 60px;
	}
	&_article {
		.card-item {
			&__main {
				padding-left: 20px;
				padding-right: 20px;
			}
			&__title {
				margin-bottom: 14px;
				a {
					border: 0;
				}
			}
			&__main {
				padding-top: 30px;
				padding-bottom: 23px;
			}
		}
	}
	&_equipment {
		.card-item {
			&__title {
				a {
					border: 0;
				}
			}
			&__img {
				height: 274px;
			}
			&__desc {
				margin-bottom: 15px;
			}
		}
	}
	&_service {
		.card-item {
			&__desc {
				font-size: 14px;
				line-height: 20px;
			}
		}
	}
}

.card-price {
	display: inline-block;
	font-size: 14px;
	line-height: 20px;
	position: relative;
	z-index: 10;
	margin-bottom: 20px;
	&__val {
		font-size: 20px;
		line-height: 20px;
		font-weight: 700;
	}
	sup {
		font-size: 10px;
	}

	&_inderline {
		&:after {
			content: "";
			position: absolute;
			left: 0;
			bottom: 2px;
			z-index: -1;
			width: 100%;
			height: 4px;
			background: $gray2;
		}
	}

	&_mod-large {
		font-size: 16px;
		line-height: 24px;
		.card-price {
			&__val {
				font-size: 36px;
				line-height: 44px;
				margin: 0 4px;
			}
		}
	}
}

.card-info {
	margin-bottom: 30px;
	font-size: 14px;
	line-height: 20px;
	&__row {
		display: flex;
		margin-bottom: 13px;
		&:last-child {
			margin-bottom: 0;
		}
		&>div {
			&:first-child {
				width: 110px;	
				flex-shrink: 0;
			}
		}
	}
	a {
		border-bottom: 1px solid rgba(0,0,0,.2);
		&:hover {
			border-color: transparent;	
		}
	}

}

.card-top-bar {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-bottom: 30px;
	.card-price {
		margin-bottom: 0px;
	}
}

.pagination {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	margin-top: 30px;
	a {
		font-size: 20px;
		line-height: 20px;
		font-weight: 700;
		margin: 0 23px;
		position: relative;
		&.active {
			&:after {
				content: "";
				position: absolute;
				left: 50%;
				bottom: -20px;
				height: 1px;
				width: 40px;
				transform: translateX(-50%);
				background: $black;
				cursor: default;
			}
		}
	}
	span {
		color: $gray;
		font-size: 20px;
	}
}

.about-block {
	padding-top: 100px;
	padding-bottom: 100px;
	p {
		font-size: 16px;
		line-height: 24px;
		margin-bottom: 25px;
		&:last-child {
			margin-bottom: 0;	
		}
	}
}

.filter-title {
	font-weight: 700;
	cursor: pointer;
	.icon {
		flex-shrink: 0;
		margin-right: 15px;
		width: 12px;
		height: 10px;
		transform: rotate(180deg);
	}
	&__main {
		position: relative;
	}
	&.selected {
		.filter-title {
			&__main {
				&:after {
					content: "";
					position: absolute;
					right: -3px;
					top: -5px;
					width: 4px;
					height: 4px;
					background: $red;
				}
			}
		}
	}
}

.filter-list {
	list-style: none;
	&>li {
		margin-bottom: 20px;
		&:last-child {
			margin-bottom: 0;	
		}
	}

	&_head {
		margin-bottom: 15px;
	}
}

.filter-section {
	border-bottom: 1px solid $gray-light;
	padding-top: 20px;
	padding-bottom: 20px;
	&__content {
		display: none;
		padding-top: 20px;
	}
	&.active {
		.filter-title {
			.icon {
				transform: none;
			}
		}
		.filter-section {
			&__content {
				display: block;
			}
		}
	}
	&__top {
		display: flex;
		flex-wrap: wrap;
	}
}

.filter-list-row {
	display: flex;
	margin: 0 -10px;
	.filter-list {
		width: 50%;
		box-sizing: border-box;
		padding-left: 10px;
		padding-right: 10px;
	}
}

.filter-selected {
	display: inline-flex;
	border: 1px solid $gray2;
	font-size: 12px;
	line-height: 16px;
	padding: 3px 6px;
	font-weight: 400;
	border-radius: 2px;
	margin-left: 30px;
	&__close {
		flex-shrink: 0;
		width: 8px;
		height: 8px;
		margin-left: 12px;
		fill: $gray;
	}
}

.search {
	position: relative;
	flex-grow: 1;
	input {
		height: 64px;
		font-size: 16px;
		line-height: 24px;
		color: $black;
		padding-left: 75px;
		padding-right: 30px;
		background: $gray-light;
	}
	&__icon {
		position: absolute;
		left: 40px;
		top: 50%;
		transform: translateY(-50%);
		width: 16px;
		height: 16px;
	}
}

.sorting-arrow {
	width: 8px;
	height: 18px;
	display: none;
}

.catalog-top-bar {
	display: flex;
	align-items: flex-start;
	border-bottom: 1px solid $gray-light;
	position: relative;
	margin-bottom: 40px;
	&:after {
		content: "";
		position: absolute;
		left: 100%;
		bottom: -1px;
		width: 9999px;
		height: 1px;
		background: $gray-light;
	}
	.search {

	}
	.show-bar {
		padding-top: 20px;
		margin-left: 90px;
	}
	.tabs-menu {
		margin-bottom: 0;
	}
}
























