/****************************************************
buttons
****************************************************/
.btn {
	display: inline-flex;
	align-items: center;
	justify-content: center;
	box-sizing: border-box;
	transition: .3s;
	text-decoration: none;
	cursor: pointer;

	&-main {
		height: 56px;
		font-size: 16px;
		font-weight: 700;
		line-height: 24px;
		width: 240px;
		padding-left: 15px;
		padding-right: 15px;
		background: $black;
		color: $white;
		&_mod-white {
			background-color: $white;
			color: $black;
		}
	}

	&-standart {
		position: relative;
		font-weight: 700;
		height: 45px;
		padding-left: 20px;
		padding-right: 20px;
		min-width: 160px;
		&__figure1 {
			&:before {
				content: "";
				position: absolute;
				left: 0;
				top: 0;
				width: 2px;
				height: 16px;
				background: $black;
				transition: all 0.3s;
			}
			&:after {
				content: "";
				position: absolute;
				left: 0;
				top: 0;
				width: 50px;
				height: 2px;
				background: $black;
				transition: all 0.3s;
			}
		}
		&__figure2 {
			&:before {
				content: "";
				position: absolute;
				right: 0;
				bottom: 0;
				width: 2px;
				height: 16px;
				background: $black;
				transition: all 0.3s;
			}
			&:after {
				content: "";
				position: absolute;
				right: 0;
				bottom: 0;
				width: 50px;
				height: 2px;
				background: $black;
				transition: all 0.3s;
			}
		}
		&:hover{
			color: #333;
			span{
				&:before,
				&:after{
					background-color: #333;
				}
			}
		}
		&:active{
			color: #000;
			span{
				&:before,
				&:after{
					background-color: #000;
				}
			}
		}
		&.disabled,
		&:disabled{
			color: #8F8F8F;
			span{
				&:before,
				&:after{
					background-color: #8F8F8F;
				}
			}
		}
	}

	&-search {
		width: 16px;
		height: 16px;
	}

}


.link-more {
	display: inline-flex;
	align-items: center;
	font-size: 16px;
	line-height: 24px;
	font-weight: 700;
	.icon {
		flex-shrink: 0;
		margin-left: 10px;
		width: 8px;
		height: 12px;
	}
}

.link-file {
	display: inline-flex;
	align-items: center;
	font-size: 14px;
	.icon {
		flex-shrink: 0;
		margin-right: 15px;
		width: 16px;
		height: 16px;
	}
	&__text {
		line-height: 18px;
		border-bottom: 1px solid rgba(0,0,0,.2);
		transition: .3s;
	}
	&:hover {
		.link-file {
			&__text {
				border-color: transparent;
			}
		}
	}
}





