/****************************************************
page: articles
****************************************************/

.info-icon {
	display: flex;
	align-items: center;
	font-size: 14px;
	line-height: 17px;
	color: #A7A7A7;
	.icon {
		flex-shrink: 0;
		margin-right: 10px;
		max-width: 20px;
		max-height: 20px;
		opacity: .4;
	}
}

.section-content {
	padding-top: 28px;
	padding-bottom: 80px;
}

.text-container {
	p {
		font-size: 16px;
		line-height: 31px;
		margin-bottom: 17px;
		&:last-child {
			margin-bottom: 0;	
		}
	}
	h1 {
		margin-bottom: 26px;
	}
	.video-item {
		margin-top: 60px;
		margin-bottom: 68px;
	}
}

.text-selected {
	border-left: 3px solid $black;
	background: $gray-light;
	padding: 40px 36px;
	margin-top: 60px;
	margin-bottom: 38px;
	p {
		line-height: 28px;
	}
}

.photo-items-wrapper {
	overflow: hidden;
	max-width: 1270px;
}

.photo-items {
	margin-top: 57px;
	margin-bottom: 57px;
	max-width: 1290px;
	margin-left: -10px;
	margin-right: -10px;
	overflow: hidden;
	.slick-slide {
		box-sizing: border-box;
		line-height: 0;
		padding-left: 10px;
		padding-right: 10px;
		img {
			width: 100%;
		}
	}
	.slick-arrow {
		right: 10px;
		left: auto;
	}
	.slick-prev {
		bottom: 60px;
	}
	.slick-next {
		bottom: 0px;
	}
}

.video-item {
	position: relative;
	line-height: 0;
	height: 568px;
	overflow: hidden;
	img {
		height: 100%;
		max-width: none;
		max-height: none;
	}
	&__overlay {
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		background: linear-gradient(0deg, rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6));
		z-index: 10;
		color: #fff;
	}
	&__icon {
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%,-50%);
	}
	&__title {
		font-size: 20px;
		line-height: 30px;
		font-weight: 700;
		min-height: 60px;
		position: absolute;
		width: 100%;
		left: 0;
		bottom: 22%;
		text-align: center;
		span {
			border-bottom: 1px solid transparent;
			transition: .3s;
		}
	}

	&_size-medium {
		height: 355px;
		.video-item {
			&__title {
				bottom: 12%;
			}
		}
		.icon-play {
			width: 60px;
			height: 60px;
			.icon {
				width: 24px;
				height: 24px;
			}
		}
	}

	&:hover {
		.video-item {
			&__title {
				span {
					border-color: $white;
				}
			}
			&__overlay {
				opacity: .7;
			}
		}
	}
}

.article-section {
	margin-top: 70px;
	margin-bottom: 70px;
	&:last-child {
		margin-bottom: 0;	
	}
}

.icon-play {
	border-radius: 100%;
	border: 2px solid $white;
	box-sizing: border-box;
	width: 90px;
	height: 90px;
	display: flex;
	align-items: center;
	justify-content: center;
	.icon {
		width: 36px;
		height: 36px;
		margin-left: -4px;
	}
}

.informers-row {
	display: flex;
	flex-wrap: wrap;
	margin-bottom: 60px;
	.info-icon {
		margin-right: 30px;
		color: rgba(0,0,0,.7);
		&:last-child {
			margin-right: 0;	
		}
		.icon {
			opacity: .7;
		}
	}
}

.menu-categories {
	list-style: none;
	font-size: 16px;
	line-height: 24px;
	&>li {
		margin-bottom: 16px;
		&:last-child {
			margin-bottom: 0;	
		}
		span {
			color: $gray;
			font-weight: 400;
		}
		&.active {
			font-weight: 700;

		}
	}
}

.article-card-mini {
	display: flex;
	margin-bottom: 30px;
	border-bottom: 1px solid $gray-light;
	padding-bottom: 17px;
	margin-bottom: 17px;
	&:last-child {
		padding-bottom: 0;
		margin-bottom: 0;	
		border-bottom: 0;
	}
	&__img {
		width: 91px;
		margin-right: 15px;
		flex-shrink: 0;
		line-height: 0;
	}
	&__title {
		margin-bottom: 8px;
		a {
			font-size: 16px;
			line-height: 24px;
			font-weight: 500;
		}
	}
}

.date {
	font-size: 14px;
	color: $gray;
}

.items-wrapper {
	margin-left: -20px;
	margin-right: -20px;
	.slick-slide {
		padding-left: 20px;
		padding-right: 20px;
	}
	.card-item {
		margin-bottom: 0;	
	}
}

.css-arrows-right-top {
	.slick-arrow {
		top: -120px;
		.icon {
			fill: $black;
		}
	}
	.slick-next {
		right: 10px;
	}
	.slick-prev {
		left: auto;
		right: 80px;
	}
}

.items-wrapper-mod2 {
	margin-left: -20px;
	margin-right: -20px;
	.slick-slide {
		padding-left: 20px;
		padding-right: 20px;
	}
	.card-item {
		margin-bottom: 0;
	}
}

.items-wrapper-mod3 {
	margin-left: -20px;
	margin-right: -20px;
	.slick-slide {
		padding-left: 20px;
		padding-right: 20px;
	}
	.card-item {
		margin-bottom: 0;
	}
}

.css-arrows-right {
	.slick-arrow {
		right: -41px;
		left: auto;
		.icon {
			fill: $black;
		}
	}
	.slick-next {
		top: calc(50% + 30px);
		transform: translateY(-50%);
	}
	.slick-prev {
		top: calc(50% - 30px);
		transform: translateY(-50%);
	}
}




