/****************************************************
footer
****************************************************/
footer {
	background: $black url(../img/footer-bg.svg) no-repeat right bottom;
	color: $white;
	text-align:left;
	width:100%;
	min-width: 320px;
	order: 3;	
    -webkit-order: 3;
    .wrapper{
    	display: flex;
    }
	
}
.footer-main {
	width: 640px;
	flex-shrink: 0;
	box-sizing: border-box;
	position: relative;
	margin: 40px 0 40px -80px;
	position: relative;
	align-self: flex-start;
	&__inner {
		position: relative;
		z-index: 10;
		background: $gray-dark;
		padding: 40px 80px;

	}
	&__elem {
		position: absolute;
		left: 10px;
		top: 10px;
		width: 100%;
		height: 100%;
		border: 1px solid #fff;
		box-sizing: border-box;
		z-index: 2;
	}
}
	
.footer-request {
	border-left: 1px solid $gray-dark;
	padding: 40px 80px;
	display: flex;
	flex-direction: column;
	justify-content: center;
	&__text {
		margin-bottom: 40px;
	}
}
.footer-desc {
	margin: 20px 0;
	font-size: 14px;
	line-height: 20px;
}

.footer-copyright {
	color: $gray;
	font-size: 14px;
	line-height: 20px;
}

.footer-contacts {
	width: 630px;
	flex-shrink: 0;
	padding: 50px 100px;
	box-sizing: border-box;
	align-self: center;
	a {
		color: #fff;
	}
	&__dots {
		flex-grow: 1;
		border-bottom: 1px dotted $gray;
		opacity: .5;
	}
	&__row {
		display: flex;
		margin-bottom: 20px;
		&:last-child {
			margin-bottom: 0;	
		}
		&>div {
			&:first-child {
			}
			&:last-child {
				width: 300px;
				font-weight: 700;
			}
		}
	}
}

