/****************************************************
header
****************************************************/
header {	
	width:100%;
	min-width: 320px;
	text-align:left;	
	order: 1;	
    -webkit-order: 1;
    opacity: 0;
	.wrapper {
		display: flex;
		justify-content: space-between;
		align-items: center;

		padding-top: 40px;
		padding-bottom: 40px;
		border-bottom: 1px solid $gray-light;
	}
}

.header-bar {
	display: flex;
	align-items: center;
	flex-wrap: wrap;
	margin: 0 -27px;
	&__item {
		padding: 0 27px;
	}
}

.header-nav {
	display: flex;
	flex-wrap: wrap;
	list-style: none;
	&__item{
		margin: 0 30px;
	}
	&__link{
		font-size: 14px;
		line-height: 20px;
		font-weight: 700;
	}
}

.link-region {
	border-bottom: 1px dotted;
	font-size: 14px;
	line-height: 20px;
	&:hover {
		border-color: transparent;	
	}
}

.header-phone {
	font-size: 14px;
}

.link-menu {
	display: flex;
	align-items: center;
	&__icon {
		flex-shrink: 0;
		width: 24px;
		height: 11px;
		margin-right: 14px;
	}
}


