/****************************************************
page: index
****************************************************/

.main-categories-row {
	display: flex;
}

.main-categorie {
	width: 25%;
	box-sizing: border-box;
	position: relative;
	border-right: 2px solid #fff;
	&:last-child {
		border-right: 0;	
	}
	&__img {
		filter: grayscale(1);
		line-height: 0;
	}
	&__title {
		position: absolute;
		left: 80px;
		right: 80px;
		bottom: 80px;
		color: #fff;
		font-size: 30px;
		line-height: 38px;
		font-weight: 700;
		z-index: 10;
	}
	&:after {
		content: "";
		width: 100%;
		position: absolute;
		bottom: 0;
		left: 0;
		height: 360px;
		background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.5) 100%);
	}
}

.advantage-item {
	width: 360px;
	height: 320px;
	box-sizing: border-box;
	padding: 60px;
	background: $white;
	position: absolute;
	&__title {
		font-size: 20px;
		line-height: 30px;
		font-weight: 700;
		margin-bottom: 35px;
		&:last-child {
			margin-bottom: 0;	
		}
		span {
			border-bottom: 1px dotted;
		}
	}
	&__icon {
		width: 48px;
		height: 48px;
		margin-bottom: 50px;
		&:last-child {
			margin-bottom: 0;	
		}
	}
	&__years {
		font-size: 52px;
		line-height: 52px;
	}

	&_years {
		background-image: url(../img/img-track.jpg);
		background-repeat: no-repeat;
		color: #fff;
	}
	&_formula {
		border: 1px solid $black;
		display: flex;
		flex-direction: column;
		align-items: flex-end;
		text-align: right;
	}
	&_territory {
		background-image: url(../img/img-territory.jpg);
		background-repeat: no-repeat;
	}

	&__bg-elem {
		z-index: -1;
		position: absolute;
		width: 100%;
		height: 100%;
		left: 50%;
		top: 50%;
		opacity: 0.2;
		border: 1px solid #000000;
		box-sizing: border-box;
		transform: rotate(-30deg) translate(-50%,-50%);
		transform-origin: 0 0;
	}


	&_1 {
		left: 60px;
		top: 20px;
		z-index: 10;
	}
	&_2 {
		right: 0;
		top: 0;
	}
	&_3 {
		left: 0;
		top: 320px;
		z-index: 10;
	}
	&_4 {
		right: 60px;
		top: 300px;
		z-index: 11;
	}
}

.advantages-items {
	position: relative;
	width: 760px;
	height: 660px;	
}

.informer-items {
	display: flex;
	flex-wrap: wrap;
}

.informer-item {
	background: $blue;
	border-right: 1px solid rgba(255,255,255,.15);
	padding: 40px;
	color: #fff;
	width: 220px;
	box-sizing: border-box;
	&:last-child {
		border-right: 0;	
	}
	&__value {
		font-size: 20px;
		line-height: 30px;
		font-weight: 700;
		margin-bottom: 10px;
	}
	&__text {
		font-size: 14px;
		line-height: 20px;
	}
}


















