


.section-questions {
	border-left: 1px solid $gray-light;
	padding: 50px 0;
}

.question-item {
	padding-left: 80px;
	position: relative;
	margin-bottom: 40px;
	&:last-child {
		margin-bottom: 0;	
	}
	&__title {
		span {
			font-size: 24px;
			line-height: 48px;
			border-bottom: 1px dotted $gray;
			transition: .5s;
		}
	}
	&__main {
		font-size: 16px;
		line-height: 24px;
		padding-top: 20px;
		display: none;
	}
	&:before {
		content: "";
		position: absolute;
		left: 0;
		top: 0;
		width: 1px;
		height: 100%;
		background: $black;
		opacity: 0;
		transition: .3s;
	}

	&.active {
		&:before {
			opacity: 1;
		}
		.question-item {
			&__main {
				display: block;
			}
			&__title {
				span {
					font-weight: 600;
				}
			}
		}
	}
}


.content-row {
	display: flex;
	align-items: flex-start;
}

.content-main-col {
	flex-grow: 1;
}

.content-side {
	width: 470px;
	flex-shrink: 0;
	margin-left: 80px;
}

.top-bar-line {
	background: $gray-light;
	position: relative;
	padding: 20px 0 0;
	&:before {
		content: "";
		position: absolute;
		right: 100%;
		top: 0;
		width: 9999px;
		height: 100%;
		background: $gray-light;
	}
	&__title {
		margin-right: 60px;
	}
	.tabs-menu {
		margin-bottom: 0;
	}
	&__inner {
		display: flex;
		flex-wrap: wrap;
	}
}

.note-text {
	font-size: 12px;
	line-height: 20px;
	color: $gray;
	a {
		color: $gray;
		border-bottom: 1px solid rgba(143,143,143,.2);
		&:hover {
			border-color: transparent;	
		}
	}
}

.form-btn {
	margin-bottom: 30px;
	&:last-child {
		margin-bottom: 0;	
	}
}

.form-title {
	margin-bottom: 8px;
}

.form-subtitle {
	margin-bottom: 30px;
}

.faq-side {
	display: flex;
	justify-content: flex-end;
	&.col-lg-3 {
		flex: 0 0 30%;
		max-width: 500px;
		margin-left: 40px;
	}
	.side-block {
		max-width: 100%;
	}
}

.faq-main {
	&.col-lg-9 {
		flex: 0 0 70%;
	}
}

.content-row {
	align-items: flex-start;
}








