page: materials
****************************************************/

.table-default {
	width: 100%;
	border-collapse: collapse;
	margin-bottom: 30px;
	th {
		font-weight: 700;
		text-align: left;
		font-size: 16px;
		line-height: 24px;
		padding: 0 15px 10px;
	}
	tr {
		transition: .3s;
		border-bottom: 1px solid $gray-light;
		&:hover {
			background: $gray-light;
		}
		&:first-child {
			&:hover {
				background: none;
			}
		}
		&:last-child {
			border-bottom: 0;	
		}
	}
	td {
		font-size: 14px;
		line-height: 20px;
		padding: 10px 15px;
	}
}

.doc-card {
	display: flex;
	box-sizing: border-box;
	padding: 20px;
	border: 1px solid $black;
	min-height: calc(100% - 40px);
	margin-bottom: 40px;
	&__icon {
		flex-shrink: 0;
		margin-right: 20px;
		width: 40px;
		height: 40px;
	}
	&__title {
		margin-bottom: 10px;
		font-weight: 700;
	}
	&__main {
		display: flex;
		justify-content: space-between;
		flex-direction: column;
	}
}

.link-icon {
	display: inline-flex;
	align-items: center;
	.icon {
		flex-shrink: 0;
		width: 16px;
		height: 16px;
		margin-right: 12px;
		margin-top: -3px;
	}
	&__text {
		font-size: 14px;
		line-height: 14px;
		border-bottom: 1px dotted;
		transition: .3s;
	}
	&:hover {
		.link-icon {
			&__text {
				border-color: transparent;
			}
		}
	}
}



























