/****************************************************
uikit
****************************************************/
h1 {
	font-weight: bold;
	font-style: normal;
	font-size: 52px;
	line-height: 60px;
	padding: 15px 0;
}

h2 {
	font-weight: bold;
	font-style: normal;
	font-size: 36px;
	line-height: 44px;
	margin-bottom: 15px 0;
}

h3 {
	font-weight: bold;
	font-style: normal;
	font-size: 28px;
	line-height: 44px;
	padding:  15px 0;
}

h4 {
	font-weight: bold;
	font-style: normal;
	font-size: 20px;
	line-height: 30px;
	padding:  15px 0;
}





.tags {
	line-height: 16px;
	a {
		color: $gray;
		font-size: 12px;
		line-height: 16px;
	}	
}

.ui-list{
	margin: 0 -30px;
	&__item{
		display: flex;
		align-items: center;
		margin-bottom: 10px;
		padding: 0 30px;
		&:last-child{
			margin-bottom: 0;
		}
	}
	&__tags{
		width: 50%;
	}
	&__value{
		font-weight: 300;
		font-size: 14px;
		line-height: 20px;
		color: #000000;
		width: 50%;
	}
}





























