/****************************************************
page: cases
****************************************************/
.cases-wrapper {
	display: flex;
	flex-wrap: wrap;
	margin-left: -20px;
	margin-right: -20px;
	&__col {
		padding-left: 20px;
		padding-right: 20px;
		width: 25%;
		box-sizing: border-box;
	}
}


.case-card {
	&__main {
		display: flex;
	}
	&__desc {
		box-sizing: border-box;
		padding-left: 80px;
		line-height: 28px;
		p {
			margin-bottom: 33px;
		}
		&__line {
			margin-bottom: 35px;
		}
	}

	&__section-main {
		background: #FDFDFD;
		padding-top: 28px;
		padding-bottom: 80px;
	}
	&__section-reviews {
		background: #FDFDFD;
		padding-top: 112px;
		padding-bottom: 137px;
		.title {
			margin-bottom: 85px;
		}
	}
	&__section-materials {
		padding-top: 20px;
		padding-bottom: 50px;
	}
	&__section-steps {
		padding-top: 30px;
		padding-bottom: 123px;
	}
	&__section-default {
		padding-top: 20px;
	}
}

.col-side {
	.side-block {
		margin-left: 40px;
	}
}

.side-block {
	max-width: 370px;
	flex-shrink: 0;
	border: 1px solid $black;
	background: $white;
	margin-bottom: 50px;
	position: relative;
	&:after {
		content: "";
		position: absolute;
		width: 100%;
		height: 100%;
		background: $gray-light;
		left: 10px;
		top: 10px;
		z-index: -1;
	}
	&__section {
		padding: 30px;
		border-bottom: 1px solid $black;
		&:last-child {
			border-bottom: 0;	
		}
		h4 {
			&:last-child {
				margin-bottom: 0;
			}
		}
	}

	.card-parameters {
		font-size: 14px;
		line-height: 20px;
		margin-bottom: 50px;
		&__row {
			margin-bottom: 18px;
			&>div {
				&:first-child {
					font-weight: 700;
					width: 95px;
					flex-shrink: 0;
				}
			}
		}
	}

	&_form {
		.side-block {
			&__section {
				padding: 40px;
			}
		}
	}

	&__line {
		margin-bottom: 13px;	
		&:last-child {
			margin-bottom: 0;
		}	
	}
}

.text-note {
	font-style: italic;
	font-size: 14px;
	line-height: 21px;
	color: $gray;
	margin-bottom: 35px;
	&:last-child {
		margin-bottom: 0;
	}	
}


.gallery-slider {
	width: 640px;
	&__main {
		margin-bottom: 10px;
		line-height: 0;
	}
	&__preview {
		padding-left: 34px;
		padding-right: 34px;
		margin: 0 -2px;
		.slick-slide {
			cursor: pointer;
			margin: 0 2px;
			border: 1px solid transparent;
			box-sizing: border-box;
			line-height: 0;
			&.slick-current {
				border-color: $black;
			}
		}
	}
}


.section-top-bar {
	border-bottom: 1px solid $gray-light;
	margin-bottom: 65px;
}

.tabs-menu {
	list-style: none;
	display: flex;
	flex-wrap: wrap;
	&>li {
		margin-right: 61px;
		position: relative;
		padding-bottom: 20px;
		&:last-child {
			margin-right: 0;	
		}
		&>a {
			font-size: 16px;
			line-height: 24px;
			font-weight: 700;
			color: $gray;
			border-bottom: 1px dotted;
		}
		&:after {
			content: "";
			position: absolute;
			left: 0;
			bottom: 0;
			width: 100%;
			height: 1px;
			background: $black;
			opacity: 0;
			transition: .3s;
		}
		&.active {
			&>a {
				color: $black;
				border-color: transparent;
			}
			&:after {
				opacity: 1;
			}
			.sorting-arrow {
				display: inline-block;
			}
		}
	}

	&_side {
		flex-wrap: nowrap;
		justify-content: space-between;
		margin-bottom: 20px;
		&>li {
			margin-right: 15px;
		}
	}
}


.review-item {
	position: relative;
	z-index: 10;
	&__inner {
		background: #fff;
		padding: 60px 77px 65px;
		position: relative;
		&:before {
			content: "";
			position: absolute;
			left: -20px;
			top: -20px;
			width: 100%;
			height: 100%;
			background: $gray-light;
			z-index: -1;
		}
		&:after {
			content: "";
			position: absolute;
			right: -15px;
			bottom: -15px;
			width: 100%;
			height: 100%;
			border: 1px solid $black;
			box-sizing: border-box;
			z-index: -1;
		}
	}
	&__text {
		font-style: italic;
		font-size: 14px;
		line-height: 31px;
		margin-bottom: 43px;
	}
	&__info {
		display: flex;
		margin-top: 30px;
	}
	&__logo {
		flex-shrink: 0;
		width: 102px;
		margin-right: 30px;
	}
	&__author {
		margin-bottom: 7px;
	}
	&__author-about {
		color: $gray;
		font-size: 14px;
		line-height: 20px;
	}
}

.step-item {
	border: 1px solid $black;
	display: flex;
	margin-bottom: 38px;
	&:last-child {
		margin-bottom: 0;	
	}
	&__img {
		width: 410px;
		line-height: 0;
	}
	&__main {
		width: 550px;
		box-sizing: border-box;
		padding: 30px 40px;
		border-right: 1px solid $black;
	}
	&__text {
		font-size: 14px;
		line-height: 20px;
	}
	&__col {
		width: 240px;
		box-sizing: border-box;
		padding: 30px 40px;
	}
}

.text-title {
	margin-bottom: 13px;
}

.list-numeric {
	font-weight: 600;
	margin-bottom: 17px;
	span {
		font-weight: 400;
	}
	&>li {
		margin-bottom: 10px;
		&:last-child {
			margin-bottom: 0;	
		}
	}
}


.cases-map {
	background: #ccc;
	min-height: 980px;
	box-sizing: border-box;
	padding-top: 28px;
}

.title-bar {
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.show-bar {
	display: flex;
	flex-wrap: wrap;
	&__title {
		margin-right: 40px;
	}
	.tabs-menu {
		&>li {
			margin-right: 40px;
			&:last-child {
				margin-right: 0;	
			}
		}
	}
}

.section-def {
	padding-top: 73px;
	padding-bottom: 120px;
}

.btn-center {
	text-align: center;
	&_offset-top {
		margin-top: 20px;
	}
}

.cases-top-bar {
	display: flex;
	justify-content: space-between;
	margin-bottom: 65px;
	&__show {

	}
}



